import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalService } from '../modal';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-stickyfooter',
  templateUrl: './stickyfooter.component.html',
  styleUrls: ['./stickyfooter.component.scss'],
})
export class StickyfooterComponent implements OnInit {
  public DownloadForm: FormGroup;
  submitted = false;
  bodyText: string;

  constructor(private formBuilder: FormBuilder, private modalService: ModalService, private httpClient: HttpClient) { }

  get f() {
    return this.DownloadForm.controls;
  }

  onSubmit() {
    this.submitted = true;
    if (this.DownloadForm.invalid) {
      return;
    }
    var objData = JSON.stringify({
      firstname: this.DownloadForm.value.FirstName,
      lastname: this.DownloadForm.value.LastName,
      email: this.DownloadForm.value.Email,
      segment3: 'download',
    })
    return this.httpClient.post(
      `https://auto.faciletechnolab.com/DownloadRequest`,
      objData
    ).subscribe(result => {
      this.closeModal("custom-modal-1");
      window.open(
        "https://github.com/FacileTechnolab/FacileTechnolab.github.io/releases/download/1.0/HTML-React-develop.zip",
        "_blank"
      );
    });

  }

  onSubmit1() {
    this.submitted = true;
    if (this.DownloadForm.invalid) {
      return;
    }
    var objData = JSON.stringify({
      firstname: this.DownloadForm.value.FirstName,
      lastname: this.DownloadForm.value.LastName,
      email: this.DownloadForm.value.Email,
      segment3: 'hireus',
    })
    return this.httpClient.post(
      `https://auto.faciletechnolab.com/DownloadRequest`,
      objData
    ).subscribe(result => {
      this.closeModal("custom-modal-2");
    });
  }

  ngOnInit() {
    this.DownloadForm = this.formBuilder.group({
      FirstName: [null, [Validators.required]],
      LastName: [null, [Validators.required]],
      Email: [null, [Validators.required, Validators.email]],
    });
  }

  openModal(id: string) {
    this.submitted = false;
    this.modalService.open(id);
  }

  closeModal(id: string) {
    this.submitted = false;
    this.modalService.close(id);
  }
}
