import { Component, OnInit } from '@angular/core';
declare var jQuery;
@Component({
  selector: 'app-applicationscreenshots',
  templateUrl: './applicationscreenshots.component.html',
  styleUrls: ['./applicationscreenshots.component.scss'],
})
export class ApplicationscreenshotsComponent implements OnInit {

  constructor() { }

  ngOnInit() {}
  ngAfterViewInit(){
    
    (function ($)
  { "use strict"


/* 5.  Applic App */
    var client_list = $('.app-active');
    if(client_list.length){
      client_list.owlCarousel({
        slidesToShow: 3,
        slidesToScroll: 1,
        loop: true,
        autoplay:true,
        speed: 3000,
        smartSpeed:2000,
        dots: false,
        margin: 15,
        nav  : false,
        navText : ["<i class='fas fa-arrow-left'></i>","<i class='fas fa-arrow-right'></i>"],
        autoplayHoverPause: true,
        responsive : {
          0 : {
            items: 1
          },
          768 : {
            items: 2
          },
          992 : {
            items: 2
          },
          1200:{
            items: 4
          }
        }
      });
    }
})(jQuery);

  }

}
