import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { HomepageComponent } from './Pages/homepage/homepage.component';
import { FeaturepageComponent } from './Pages/featurepage/featurepage.component';
import { LayoutpageComponent} from './Pages/layoutpage/layoutpage.component';
import { ServicespageComponent } from './Pages/servicespage/servicespage.component';
import { PricingpageComponent } from './Pages/pricingpage/pricingpage.component';
import { BlogpageComponent } from './Pages/blogpage/blogpage.component';
import { BlogdetailspageComponent } from './Pages/blogdetailspage/blogdetailspage.component';
import { ElementspageComponent } from './Pages/elementspage/elementspage.component';
import { ContactpageComponent } from './Pages/contactpage/contactpage.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
const routes: Routes = [
  { path: "angular", component: HomepageComponent , pathMatch: 'full'},
  { path: 'angular/feature', component:FeaturepageComponent },
  { path: 'angular/home', component:HomepageComponent },
  { path: 'angular/services', component:ServicespageComponent},
  { path: 'angular/pricing', component:PricingpageComponent },
  { path: 'angular/blog', component:BlogpageComponent},
  { path: 'angular/single-blog', component:BlogdetailspageComponent},
  { path: 'angular/element', component:ElementspageComponent},
  { path: 'angular/contact', component:ContactpageComponent},
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })   
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
